/* ===============================
 DROPDOWN MENU
================================*/
.navbar-expand-lg.trans-navigation .navbar-nav .nav-link.dropdown-toggle {
  padding-right: 0;
}

.dropdown-toggle i {
  font-size: 0.625rem;
  vertical-align: 0.125rem;
}

.dropdown-menu {
  padding: 0;
  border: 0;
  box-shadow: 0.125rem 0.125rem  0.938rem 0.063rem rgba(0, 0, 0, 0.05), -0.125rem 0  0.938rem 0.063rem rgba(0, 0, 0, 0.05);
}
@media (max-width: 992px) {
  .dropdown-menu {
    text-align: center;
    float: left !important;
    width: 100%;
    margin: 0;
  }
  html{
    font-size: 70%;
  }
}
.dropdown-menu li:first-child {
  margin-top:  0.938rem;
}
.dropdown-menu li:last-child {
  margin-bottom:  0.938rem;
}

.dropdown-toggle::after {
  display: none;
}

.dropleft .dropdown-menu,
.dropright .dropdown-menu {
  margin: 0;
}

.dropleft .dropdown-toggle::before,
.dropright .dropdown-toggle::after {
  font-weight: bold;
  font-family: "Font Awesome 5 Free";
  border: 0;
  font-size: 0.625rem;
  vertical-align: 0.063rem;
}

.dropleft .dropdown-toggle::before {
  content: "\f053";
  margin-right:  0.938rem;
}

.dropright .dropdown-toggle::after {
  content: "\f054";
  margin-left:  0.938rem;
}

.dropdown-item {
  padding: 0.6rem 1.5rem 0.35rem;
  text-transform: uppercase;
  font-size: 0.875rem;
  font-weight: 500;
}

.dropdown-submenu.active > .dropdown-toggle,
.dropdown-submenu:hover > .dropdown-item,
.dropdown-item.active,
.dropdown-item:hover {
  background: rgba(99, 92, 219, 0.1);
  color: #635CDB;
}

button:focus {
  outline: 0;
}

ul.dropdown-menu li {
  padding-left: 0 !important;
}

@media (min-width: 992px) {
  .dropdown-menu {
    transition: all 0.2s ease-in, visibility 0s linear 0.2s, transform 0.2s linear;
    display: block;
    visibility: hidden;
    opacity: 0;
    min-width: 12.5rem;
    transform: translateY(0.625rem);
  }
  .dropdown-menu li:first-child {
    margin-top:  0.938rem;
  }
  .dropdown-menu li:last-child {
    margin-bottom:  0.938rem;
  }
  .dropleft .dropdown-menu,
.dropright .dropdown-menu {
    margin-top: -0.313rem;
  }
  .dropdown:hover > .dropdown-menu {
    visibility: visible;
    transition: all 0.3s ease 0s;
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 992px) {
  .navbar-collapse {
    text-align: center;
  }
}
.navbar-collapse.show {
  overflow-y: auto;
  max-height: calc(100vh - 6.25rem);
}

